import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service';
import { AlertController } from '@ionic/angular';
import { Router } from '@angular/router';
@Component({
  selector: 'app-registraion',
  templateUrl: './registraion.page.html',
  styleUrls: ['./registraion.page.scss'],
  providers: [HttpService]
})
export class RegistraionPage implements OnInit {
  firstName:any;
  lastName:any;
  phone:any;
  email:any;
  password:any;
  response:any;
  currentColor:any;
  constructor(public router: Router,public shared: HttpService,public alertController: AlertController) {
 
  //  this.currentColor='dark'

   }

  ngOnInit() {
  }



  blockSpecialChar(e) {
    var charCode = (e.which) ? e.which : e.keyCode
         if (charCode > 31 && (charCode < 48 || charCode > 57))
            return false;

         return true;

}

  registration(){
    
    if(this.firstName==undefined||this.firstName==""){
      this.shared.displayNetworkUpdate('First Name cannot be blank')

    }else if(this.lastName==undefined||this.lastName==""){
      this.shared.displayNetworkUpdate('Last Name cannot be blank')
    }else if(this.email==undefined||this.email==""){
      this.shared.displayNetworkUpdate('Email cannot be blank')
    }else if(this.password==undefined||this.password==""){
      this.shared.displayNetworkUpdate('Password cannot be blank')
    }else{
    let data= this.validateEmail(this.email)

    if (data==false){
      this.shared.displayNetworkUpdate('Please check your email ID')
    }else{

      if (navigator.onLine) {
        this.shared.ServiceCallRegistraion.ServiceCallRequestRegistraionData(this.firstName,this.lastName,this.phone,this.email,this.password).then((res) => { 
          this.response=res;
          // this.shared.displayNetworkUpdate(this.response.content)
          this.presentAlert(this.response.content);
    
          }, err => {
       
          })
    
      }else{
        this.shared.displayNetworkUpdate('No Internet Connection')
    
      }

    }

    }

  }


validateEmail(email) 
{
  var re = /^(?:[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/;
  return re.test(email);
}


async presentAlert(message) {
  const alert = await this.alertController.create({
    header: 'Alert',
    message: message,
    buttons: [{
      text: 'Okay',
      handler: () => {
        this.router.navigate(['/menu/first/tabs/tab1/login']);
        
      } }]
  });

  await alert.present();
}

}
